"use strict";
var Data_Int = require("../Data.Int");
var Data_Number_Format = require("../Data.Number.Format");
var Data_Semigroup = require("../Data.Semigroup");
var Data_Semiring = require("../Data.Semiring");
var Game_Types = require("../Game.Types");
var Halogen_VDom = require("../Halogen.VDom");
var Prelude = require("../Prelude");
var UI_Core = require("../UI.Core");
var UI_Elements = require("../UI.Elements");
var UI_Events = require("../UI.Events");
var UI_Properties = require("../UI.Properties");
var baloonDraw = function (s) {
    return function (idpos) {
        return UI_Elements.imageView([ UI_Properties.id_(idpos.id), UI_Properties.width("50"), UI_Properties.height("100"), UI_Properties.margin(Data_Number_Format.toString(Data_Int.toNumber(idpos.x)) + ("," + (Data_Number_Format.toString(Data_Int.toNumber(idpos.y)) + ",0,0"))), UI_Properties.imageUrl(idpos.image) ]);
    };
};
var arrowDraw = function (s) {
    return function (id) {
        return UI_Elements.imageView([ UI_Properties.id_(id.id), UI_Properties.width("60"), UI_Properties.height("7"), UI_Properties.margin(Data_Number_Format.toString(Data_Int.toNumber(id.x)) + ("," + (Data_Number_Format.toString(Data_Int.toNumber(id.y + 47 | 0)) + ",0,0"))), UI_Properties.imageUrl("arrow") ]);
    };
};
module.exports = {
    baloonDraw: baloonDraw,
    arrowDraw: arrowDraw
};



